import React from "react";
import Cell from "./cell.jsx";

const Board = (
  { handlechange, MazeValue,  userClick, setuserClick,disabledSquare,setdisabledSquare, setstartGameText },
  props
) => {
  const renderCell = (value) => {
    return (
      <Cell
        currentSquare={MazeValue.currentSquare}
        value={value}
        handleRegionClick={handleRegionClick}
        startSquare={MazeValue.startSquare}
        startIcon={MazeValue.startIcon}
        resultIcon={MazeValue.resultIcon}
        userClick={userClick}
        disabledSquare={disabledSquare}
      />
    );
  };

  const handleRegionClick = (id) => {
    setdisabledSquare(true)
    if (MazeValue.currentSquare === id) {
      if (id === MazeValue.startSquare) {
        handlechange("startIcon", "ü§ñ");
      }

      handlechange("resultIcon", "ü§ñ");
      setuserClick(false);
      handlechange("resultText", `–ü–æ–∑–¥—Ä–∞–≤–ª—è—é —Å –ø–æ–±–µ–¥–æ–π`);
   
      setstartGameText('–ü—Ä–æ–¥–æ–ª–∂–∏—Ç—å')
      handlechange("gamePoints", MazeValue.gamePoints+1)
    }
    else if (MazeValue.currentSquare === null){ 
        handlechange(
            "resultText",
            `–í—ã –Ω–µ –Ω–∞—á–∞–ª–∏ –∏–≥—Ä—É :)`
          );
    }
    else {
        if (id === MazeValue.startSquare) {
            handlechange("startIcon", "üí•");
         }
      handlechange("resultIcon", "üí•");
      setuserClick(true);
      handlechange(
        "resultText",
        `–ö —Å–æ–∂–∞–ª–µ–Ω–∏—é –≤—ã –ø—Ä–æ–∏–≥—Ä—ã–ª–∏, –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –±–ª–æ–∫ –Ω–∞ ${MazeValue.currentSquare + 1} —è—á–µ–π–∫–µ`
      );
      setstartGameText('–ù–∞—á–∞—Ç—å —Å–Ω–∞—á–∞–ª–∞')
      handlechange("gamePoints", 0)
    }
    setTimeout (() => {
      handlechange("currentSquare", null)
    },10000)
  };

  return (
    <div>
      <div className="board-row">
        {renderCell(0)}
        {renderCell(1)}
        {renderCell(2)}
      </div>
      <div className="board-row">
        {renderCell(3)}
        {renderCell(4)}
        {renderCell(5)}
      </div>
      <div className="board-row">
        {renderCell(6)}
        {renderCell(7)}
        {renderCell(8)}
      </div>
    </div>
  );
};

export default Board;
