import React from "react";

const Cell = ({
  value,
  handleRegionClick,
  MazeValue,
  startSquare,
  startIcon,
  userClick,
  resultIcon,
  currentSquare,
  disabledSquare,
  ...props
}) => {

  if (value === startSquare) {
    return (
      <button
        className="square"
        key={value}
        disabled={disabledSquare}
        onClick={() => handleRegionClick(value)}
      >
        
        {startIcon}
      </button>
    );
  } else if (value === currentSquare) {
    return (
      <button
        className="square"
        key={value}
        disabled={disabledSquare}
        onClick={() => handleRegionClick(value)}
      >
        
        {userClick ? "üí•" : resultIcon}
      </button>
    );
  } else {
    return (
      <button
        className="square"
        key={value}
        disabled={disabledSquare}
        onClick={() => handleRegionClick(value)}
      >
        {"üí£"}
      </button>
    );
  }
};

export default Cell;
